<!-- 收徒任务首页 -->
<template>
  <div class="page hotapps-page" v-nav="{hideNavbar: true}">
    <!-- 页头 -->
    <AnimateHeader
      bg-color="#FDFDFD"
      menu-bg-color="#F8F8F8"
      color="#3a3a3a"
      title="装机必备"
      sub-title="“这都是我们亲手挑选的，希望你喜欢！”"
      :on-back-button-click="back"
      :expanded="true"
      back-arrow-height="22px"
      back-arrow-width = "13px"
    >
    </AnimateHeader>

    <div class="page-content has-animate-header bg-light">
      <div class="classify-title">
        <router-link
          :class="{'on':curIdx===index}"
          v-for="(item, index) in tabLinks"
          :key="index"
          :to="item.to"
        >
          <span
            v-text="item.text"
          ></span>
        </router-link>
      </div>
      <div class="list">
        <BoutiqueComponent
          :down-lists="downLists"
          :on-list-click="goToDetail"
          :on-download-button-click="goToDownload"
          v-show="curIdx==0"
        >
        </BoutiqueComponent>
        <Classify
          :classify-lists="classifyLists"
          :on-go-to-all-click="goToAll"
          :on-go-to-single-click="goToSignle"
          v-show="curIdx==1"
        ></Classify>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
  .hotapps-page {
    .bg-light {
      padding-top: 100px;
      background: #FFF;
    }

    .animate-header .header-content p.light {
      color: #3a3a3a;
    }

    .classify-title {
      height: 46px;
      display: flex;
      display: -webkit-flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 16px;

      a {
        text-decoration: none;
        display: block;
        width: 50%;
        height: 46px;
        line-height: 46px;
        text-align: center;

        span {
          font-size: 14px;
          color: #3A3A3A;
          padding: 0 5px;
        }

        &.router-link-active {
          span {
            border-bottom: 2px solid #3A3A3A;
          }
        }
      }
    }

    g {
      stroke: black !important;
      fill: black !important;
    }
  }
</style>

<script>
  import { hotapps_path } from 'constants'
  /* eslint eqeqeq: "off", camelcase: "off" */
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  // 精品下载组件
  import BoutiqueComponent from './BoutiqueComponent.vue'
  // 分类 组件
  import Classify from './Classify.vue'

  import {appendUrlParams} from 'utils'

  export default {
    components: {
      AnimateHeader,
      BoutiqueComponent,
      Classify
    },
    created () {
      this.curIdx = _.indexOf(['boutique', 'classify'], this.$route.params.type)
      this.fetch(this.curIdx)
    },
    mounted () {
    },
    watch: {
      '$route.params.type': function (val) {
        this.curIdx = _.indexOf(['boutique', 'classify'], this.$route.params.type)
        this.fetch(this.curIdx)
      }
    },
    data () {
      return {
        tabLinks: [
          {
            to: { path: '/hotapps/boutique' },
            text: '精品'
          },
          {
            to: { path: '/hotapps/classify' },
            text: '分类'
          }
        ],
        curIdx: 0,
        downLists: [],
        classifyLists: []
      }
    },
    methods: {
      back () {
        let path = _.get(this.$route.query, 'backroute', '/dashboard')
        $router.back({ path })
      },
      fetch (index) {
        if (index === 0) {
          $loading.show('加载中...')
          $api.getAppIndex()
            .then((res) => {
              if (res.data.err_code === 0) {
                $loading.hide()
                this.downLists = res.data && res.data.payload
              }
            })
        } else {
          $loading.show('加载中...')
          $api.getAppCategoryList()
            .then((res) => {
              if (res.data.err_code === 0) {
                $loading.hide()
                this.classifyLists = res.data && res.data.payload
              }
            })
        }
      },
      goToDownload (app_id, ct, url) {
        $api.installDownloadRecord(app_id, ct)
        location.href = appendUrlParams(url, {
          'return_url': location.href
        })
      },
      goToDetail (app_id) {
        let fromPath = this.$route.path
        let _path = `/hotapps/appDetail/${app_id}`
        $storage.set(hotapps_path, fromPath)
        this.$router.push({
          path: _path
        })
      },
      goToSignle (parent_id, category_id) {
        let id = parent_id + '_' + category_id
        this.$router.push({
          path: `/hotapps/typeDetails/${id}`
        })
      },
      goToAll (parent_id) {
        this.$router.push({
          path: `/hotapps/typeDetails/${parent_id}_0`
        })
      }

    }
  }
</script>



