<template>
  <div class="boutique-download">
    <div class="boutique-item" v-for="item in downLists">
      <div class="title" v-text="item.name"></div>
      <div class="download-wrap">
        <ul>
          <li v-for="list in item.list" @click="goToDetail(list.app_id)">
            <div>
              <img :src="list.icon" alt="">
            </div>
            <div @click="go">
              <span v-text="list.app_name"></span>
              <span v-text="list.intro"></span>
            </div>
            <div @click.stop="goToDownload(list.app_id, list.ct, list.download_url)">下载</div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  .boutique-download {
    padding: 0 16px;

    .title {
      font-family: 'PingFang-SC-Medium';
      color: #3a3a3a;
      text-align: center;
      font-size: 16px;
      line-height: 18px;
      margin-bottom: 10px;
      font-weight: bold;
    }

    .boutique-item {
      margin-bottom: 16px;
    }

    .download-wrap ul li {
      display: flex;
      display: -webkit-flex;
      justify-content: flex-start;
      align-items: center;
      padding: 15px 0;

      div:nth-child(1)::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        border: 1px solid #dddddd;
        width: 100px;
        height: 100px;
        transform: scale(0.5, 0.5);
        box-sizing: border-box;
        transform-origin: 0 0;
        border-radius: 6px;
      }

      div:nth-child(1) {
        position: relative;
        margin-right: 10px;
        border-radius: 6px;

        img {
          display: block;
          padding: 2px;
          width: 50px;
          height: 50px;
          border-radius: 6px;
        }
      }

      div:nth-child(2) {
        display: flex;
        display: -webkit-flex;
        flex-direction: column;
        justify-content: space-around;
        width: 64%;
        margin-right: 10px;

        span:nth-child(1) {
          font-family: 'PingFangSC-Regular';
          font-size: 16px;
          color: #3A3A3A;
          height: 20px;
          line-height: 20px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          -webkit-box-orient: vertical;
          margin-bottom: 5px;
        }

        span:nth-child(2) {
          font-size: 12px;
          color: #B5B5B5;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }

      div:nth-child(3) {
        text-align: center;
        width: 48px;
        height: 26px;
        line-height: 24px;
        border: 1px solid #4a90e2;
        border-radius: 2px;
        color: #4a90e2;
      }
    }
  }
</style>
<script>
  export default {
    components: {},
    props: {
      downLists: Array,
      onListClick: Function,
      onDownloadButtonClick: Function
    },
    data () {
      return {}
    },
    mounted () {

    },
    methods: {
      goToDetail (app_id) {
        if (this.onListClick) {
          this.onListClick(app_id)
        }
      },
      goToDownload (app_id, ct, url) {
        if (this.onDownloadButtonClick) {
          this.onDownloadButtonClick(app_id, ct, url)
        }
      }
    }
  }
</script>



