<template>
  <div class="classify">
    <div class="classify-item" v-for="item in classifyLists">
      <div
        class="title"
        v-text="item.title"
        @click.stop="goToAll(item.parent_id)"
      ></div>
      <div class="classify-list">
        <div>
          <span
            :style='"background-image: url(" + item.icon + ")"'
            @click.stop="goToAll(item.parent_id)"
          ></span>
        </div>
        <div>
          <ul>
            <li
              v-for="list in item.list"
              v-html="list.title"
              @click.stop="goToSingle(item.parent_id, list.category_id)"
            ></li>
          </ul>
        </div>
      </div>
    </div>

  </div>
</template>
<style lang="scss" scoped>
  .classify {
    padding: 15px;
    padding-right: 0px;

    .title {
      font-family: PingFangSC-Semibold;
      font-size: 14px;
      color: #3A3A3A;
      margin-bottom: 15px;
    }

    .classify-item {
      margin-bottom: 30px;
    }

    .classify-list {
      display: flex;
      display: -webkit-flex;
      align-items: center;

      div:nth-child(1) {
        span {
          background-repeat: no-repeat;
          background-position: center center;
          background-size: cover;
          display: block;
          padding-top: 100%;
          width: 80px;
          height: 80px;
          border-radius: 4px;
        }
      }

      div:nth-child(2) {
        width: 100%;

        ul {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;

          li {
            width: 33%;
            text-align: center;
            margin: 10px 0;
          }
        }
      }
    }
  }
</style>
<script>
  export default {
    props: {
      classifyLists: Array,
      onGoToAllClick: Function,
      onGoToSingleClick: Function
    },
    data () {
      return {
      }
    },
    methods: {
      goToAll (parent_id) {
        if (this.onGoToAllClick) {
          this.onGoToAllClick(parent_id)
        }
      },
      goToSingle (parent_id, category_id) {
        if (this.onGoToSingleClick) {
          this.onGoToSingleClick(parent_id, category_id)
        }
      }
    }
  }
</script>



